/* App.css */

body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  background-color: #f5f5f5;
}

.app-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  color: #333;
}

.pink-button {
  padding: 12px 24px;
  margin: 10px;
  font-size: 18px;
  border: none;
  border-radius: 5px;
  background-color: #ff69b4;
  color: #fff;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.pink-button:hover {
  background-color: #ff85c2;
}

.video-container {
  position: relative;
  width: 100%;
  max-width: 600px;
  margin-top: 20px;
}

.video-player {
  width: 100%;
  border-radius: 8px;
}

.overlay-close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  padding: 5px 10px;
  font-size: 16px;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.7);
  border: none;
  border-radius: 50%;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.overlay-close-button:hover {
  background-color: rgba(0, 0, 0, 0.9);
}
